@charset "UTF-8";

html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
  position: relative;
}

body {
  margin: 0;
  font-size: 14px;
  background-color: #101114;
  font-family: "Open Sans", Arial, sans-serif;
  line-height: 1.42;
}

.wrapper {
  min-height: 100%;
  height: auto;
  overflow: hidden;
  position: relative;
}

.container {
  max-width: 960px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

/*------------стили для тегов------------*/

ul {
  margin: 0;
  padding: 0;
}

ul li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: normal;
}

* {
  box-sizing: border-box;
}

.menu__item:nth-child(odd) {
  background-color: #1f2229;
}

.menu__item:nth-child(even) {
  background-color: #191c24;
}

.menu__item:last-child .menu__link {
  color: #808592;
}

.menu__item:hover {
  background-color: #2a2d37;
}

.menu__link {
  height: 75px;
  width: 100%;
  padding-left: 22px;
  padding-top: 30px;
  display: inline-block;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
}

.pay {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  padding-top: 8%;
  padding-bottom: 8%;
}

.pay:before,
.pay:after {
  content: " ";
  display: block;
}

.pay:after {
  clear: both;
}

.pay__left,
.pay__right {
  float: left;
}

.pay__left {
  width: 33.5%;
}

.pay__right {
  width: 66.5%;
}

.info {
  padding-top: 44px;
}

.info,
.order {
  background-color: #f7f8f8;
  color: #8494ab;
  padding-left: 30px;
  padding-right: 30px;
}

.description {
  padding: 26px 30px 30px;
  background-color: #373c43;
  color: #89909d;
  font-family: Arial;
  font-size: 12px;
}